.form-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1rem;
  max-width: 90%;
  margin: 0 auto;
}

.form-grid > div {
  flex: 1 1 calc(25% - 1rem);
}

.button-container {
  display: flex;
  justify-content: center;
  margin-top: 1rem;
}
.main-title {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-bottom: 5rem;
  font-family: 'DIN-Condensed-Web', sans-serif;
  font-weight: 500;
  font-size: 50px;
  text-transform: uppercase;
}
