.input-wrapper {
  position: relative;
  margin-bottom: 1rem;
}

input {
  width: 100%;
  padding: 0.5rem 0.25rem;
  font-size: 1rem;
  border: 1px solid #c4c4c4;
  border-radius: 3px;
  transition: border-color 0.3s;
}

input:focus {
  outline: none;
  border-color: #5d9cec;
}

.floating-label {
  position: absolute;
  top: -1.65rem;
  left: 0rem;
  font-size: 1.125rem;
  color: #000;
  transition: all 0.3s;
  font-weight: 600;
  line-height: 22px;
  font-family: 'Bw Modelica Light', sans-serif;
}

.floating-label.active {
  color: #5d9cec;
}

.input-error {
  border-color: #e74c3c;
}

.error-message {
  color: #e74c3c;
  font-size: 0.75rem;
  margin-top: 0.25rem;
}
